<template>
  <div class="app-container">
    <h1>MTG Query Builder</h1>
    <div>
       
      <div class="editor-container">
        <div
          ref="queryEditor"
          class="editable-area"
        >
        </div>
      </div>

      <button @click="generateScryfallQuery">Generate Query</button>
      <p class="output">
        Generated Scryfall Query: 
        <a :href="scryfallLink" target="_blank">{{ scryfallLink }}</a>
      </p>
      <p v-if="parseError" class="error">{{ parseError }}</p>
    </div>
  </div>
</template>

<script>
import { ref, watch, nextTick, onMounted  } from 'vue';
import QueryConverter from './queryConverter';
import QueryParser from './parser';
import Suggestor from './suggestor';
import { grammar } from './grammar';
import { data } from './data';

import * as monaco from "monaco-editor";

const keywords = [...new Set(Object.entries(grammar.fields).map(([k,v]) => v.keywords).flat().filter(s => s))];
const allFields = Object.entries(grammar.fields).map(([k,v]) => k);
console.log("keywords", keywords);
monaco.languages.register({ id: "mtgQueryLanguage" });

monaco.languages.setMonarchTokensProvider("mtgQueryLanguage", {
  keywords: keywords,
	typeKeywords: [],
  types: allFields,
	operators: [ "and", "or"	],
	digits: /\d+(_+\d+)*/,

	// The main tokenizer for our languages
	tokenizer: {
		root: [
			[/[{}]/, 'delimiter.bracket'],
			{ include: 'common' }
		],

		common: [
			// identifiers and keywords
			[/[a-z_$][\w$]*/, {
				cases: {
					'@typeKeywords': 'keyword',
					'@keywords': 'keyword',
          '@types' : 'type.identifier',
          '@operators': 'keyword',
					'@default': 'identifier',
				}
			}],
			[/[A-Z][\w$]*/, 'type.identifier'],  

			// whitespace
			{ include: '@whitespace' },

			// numbers
			[/(@digits)/, 'number'],

			// strings
			[/"([^"\\]|\\.)*$/, 'string.invalid'],  // non-teminated string
			[/'([^'\\]|\\.)*$/, 'string.invalid'],  // non-teminated string
			[/"/, 'string', '@string_double'],
			[/'/, 'string', '@string_single'],
		],

		whitespace: [
			[/[ \t\r\n]+/, '']
		],

		string_double: [
			[/[^\\"]+/, 'string'],
			[/\\./, 'string.escape.invalid'],
			[/"/, 'string', '@pop']
		],

		string_single: [
			[/[^\\']+/, 'string'],
			[/\\./, 'string.escape.invalid'],
			[/'/, 'string', '@pop']
		]
	},
});

monaco.languages.registerCompletionItemProvider("mtgQueryLanguage", {
  buildFields ()  {
    const entries = Object.entries(grammar.fields).filter(([k,v]) => !v.ignore).map(([k,v]) => ({label: k, kind: monaco.languages.CompletionItemKind.Field, insertText: k}));
    return entries;
  },
  buildValueSuggestions (field)  {
    if(field === 'keyword') {
      return data.keywordAbilities.map(s => ({label:s, kind: monaco.languages.CompletionItemKind.Value, insertText: s }));
    }

    if(field === 'color' || field === 'identity') {
      return data.colorSuggestions.map(s => ({
        label:`${s.label} (${s.colors.join("")})`,
        kind: monaco.languages.CompletionItemKind.Value, 
        insertText: s.value, 
        filterText: `${s.label} ${s.colors.join(" ")}`,
        sortText: `${s.colors.length}`
      }));
    }

    if(data[field]) {
      return data[field].map(s => ({
        label: s,
        kind: monaco.languages.CompletionItemKind.Value, 
        insertText: s, 
      }));
    }

    return [];
  },
  buildSuggestions(tokens) {
    if (tokens.length === 0) {
      return this.buildFields();
    }

    const lastToken = tokens[tokens.length - 1];
    const previousToken = tokens[tokens.length - 2];
    const previousPreviousToken = tokens[tokens.length - 3];
    console.log("!!!", ({lastToken, previousToken, previousPreviousToken}));
    if(!previousToken){
      return this.buildFields();
    }

    if(grammar.fields[previousToken]) {
      if( grammar.fields[previousToken].keywords) {
        return grammar.fields[previousToken].keywords.map(s => ({label:s, kind: monaco.languages.CompletionItemKind.Keyword, insertText: s }));
      } else if (grammar.fields[previousToken].mappedField) {
        return this.buildValueSuggestions(grammar.fields[previousToken].mappedField);
      }
    }

    if(keywords.includes(previousToken)) {
      const field = previousToken === "has" ? "keyword" : tokens[tokens.length - 3];
      return this.buildValueSuggestions(field);
    }

    if (['and', 'or'].includes(previousToken)) {
      // walk backwards until a keyword, then field
      let field = '';
      for(let i = tokens.length - 2; i >= 0; i --) {
        if(keywords.includes(tokens[i]) || tokens[i] === "has"){
          field = tokens[i] === "has" ? "keyword" : tokens[i -1];
          break;
        }
      }
      let suggestions = this.buildFields();
      if(field)
        suggestions = suggestions.concat(this.buildValueSuggestions(field));
      return suggestions ;
    }    

    if(previousPreviousToken && (keywords.includes(previousPreviousToken) || previousPreviousToken === "has")) {
      return [ {label: "and", kind: monaco.languages.CompletionItemKind.Operator, insertText: "and" }, {label: "or", kind: monaco.languages.CompletionItemKind.Operator, insertText: "or"}];
    }

    return [];
  },
  provideCompletionItems (model, position, context, token) {
    const textUntilPosition = model.getValueInRange({
        startLineNumber: position.lineNumber,
        startColumn: 1,
        endLineNumber: position.lineNumber,
        endColumn: position.column
    });
    const tokens = textUntilPosition.split(/\s+/);
    const suggestions = this.buildSuggestions(tokens);
    return { suggestions: suggestions };
  },
});

monaco.editor.defineTheme("mtgQueryTheme", {
	base: "vs-dark",
	inherit: true,
	rules: [
		{ token: "keyword", foreground: "808080" },
		{ token: "operator", foreground: "ff0000", fontStyle: "bold" },
		{ token: "field", foreground: "FFA500" },
		{ token: "identifier", foreground: "008800" },
	],    
	colors: {
		
	},
});

export default {
  setup() {
    const queryEditor = ref(null);
    const queryInput = ref('');
    const scryfallLink = ref('');
    const parseError = ref('');
    const queryConverter = new QueryConverter(grammar, data);

    let editorInstance = null;
    onMounted(() => {
      editorInstance = monaco.editor.create(queryEditor.value, {
        value: '',
        language: 'mtgQueryLanguage',
        theme: 'vs-dark',
        automaticLayout: true,
        scrollBeyondLastLine: false,
        minimap: { enabled: false },
        hover: false
      });

    });

    const generateScryfallQuery = () => {
      try {
        const parser = new QueryParser(grammar, data);
        const ast = parser.parse(editorInstance.getValue()); 

        if (!ast) {
          throw new Error('Invalid query structure.');
        }

        const scryfallQuery = queryConverter.convert(ast);
        scryfallLink.value = `https://scryfall.com/search?q=${encodeURIComponent(scryfallQuery)}`;
        parseError.value = '';
      } catch (error) {
        scryfallLink.value = '';
        parseError.value = `Error parsing query: ${error.message}`;
      }
    };

    return {
      queryInput,
      scryfallLink,
      parseError,
      formattedQuery: ref(''),
      generateScryfallQuery,
      queryEditor
    };
  },
};
</script>

<style>
.app-container {
  font-family: Arial, sans-serif;
  padding: 20px;
}

.editor-container {
  position: relative;
  margin-bottom: 10px;
}

.editable-area {
  width: 100%;
  min-height: 150px;
  padding: 10px;
  font-family: monospace;
  font-size: 14px;
  color: #333;
  background-color: #f9f9f9;
  border: 1px solid #ccc;
  border-radius: 4px;
  white-space: pre-wrap;
  overflow-wrap: break-word;
  outline: none;
  transition: all 0.3s ease;
}

.editable-area:empty:before {
  content: attr(placeholder);
  color: #aaa;
}

.editable-area.valid {
  border-color: #4CAF50; 
  background-color: #eaf8e3;
}

.editable-area.invalid {
  border-color: #F44336; 
  background-color: #f8e3e3; 
}

button {
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  border: none;
  cursor: pointer;
}

button:hover {
  background-color: #0056b3;
}

.output {
  margin-top: 20px;
  font-size: 14px;
}

.error {
  color: red;
  font-size: 14px;
}

.parsed-output {
  margin-top: 20px;
  padding: 10px;
  border: 1px solid #ccc;
  background-color: #f1f1f1;
}

.parsed-output h2 {
  font-size: 16px;
  margin-bottom: 10px;
}

/* Suggestions List */
.suggestions-list {
  /*position: absolute;*/
  top: 28px;
  left: 11px;
  min-width:300px;
  z-index: 10;
  background-color: #333;
  border: 1px solid #ccc;
  border-top: none;
  max-height: 150px;
  overflow-y: auto;
  list-style-type: none;
  padding: 0;
  margin: 0;
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

.suggestions-list li {
  padding: 8px;
  cursor: pointer;
}

.suggestions-list li.active {
  background-color: #007bff;
  color: #fff;
}

.suggestions-list li.child {
  padding-left: 20px;
}

.suggestions-list li.parent {
  background-color: #f0f0f0;
  font-weight: bold;
  color: #333;
}

.editor-container span {
  margin: 0;
}

/* Token Highlighting */
.token {
  font-weight: bold;
}

.field {
  color: blue;
}

.identifier {
  color: #ffad42;
}

.keyword {
  color: #1E88E5;
}

.term-creature {
  color: #FF6347;
}

.keyword-ability {
  color: #9C27B0;
}

.operator {
  font-weight: bold;
  color: #1E88E5;
}

.unary {
  color: #FF6347;
}


</style>
