export const grammar = {
  fields: {
    color: {  
      shorthand: ["c"],
      keywords: ['is', 'includes' ],
      values: ["red", "blue", "green", "white", "black", "colorless", ],
      translations: { is: "c=", includes: "c>=" },
    },
    identity: {  
      shorthand: ["id"],
      keywords: ['is', 'includes', 'for', ],
      values: ["red", "blue", "green", "white", "black", "colorless", ],
      translations: {  is: "id=", includes:"id>=", for: "id<=",  },
    },
    type: {  
      shorthand: "t",
      keywords: ['is'],
      translations: { is: "t:" },
    },
    
    costs: {  
      keywords: ['>', '>=', '=', '<=', '<'],
      translations: { '>': "cmc>", '>=':"cmc>=", '=':"cmc=", '<=':"cmc<=", '<':"cmc<" },
    },
    power: {  
      keywords: ['>', '>=', '=', '<=', '<'],
      translations: { '>': "pow>", '>=':"pow>=", '=':"pow=", '<=':"pow<=", '<':"pow<" },
    },
    toughness: {  
      keywords: ['>', '>=', '=', '<=', '<'],
      translations: { '>': "tou>", '>=':"tou>=", '=':"tou=", '<=':"tou<=", '<':"tou<" },
    },
       
    rarity: {  
      keywords: ['is'],
      translations: { is: "rarity:" },
    },   
    legal: {  
      shorthand: ["l"],
      keywords: ['in'],
      translations: { in: "f:" },
    },
    game: {  
      shorthand: ["g"],
      keywords: ['is'],
      translations: { is: "g:" },
    },
    card: {      
      keywords: ['is'],
      translations: { is: "is:" },
    },

    name: {  
      keywords: ['is', 'contains', 'startsWith', 'endsWith'],
      translations: { 
        is(value) { return `name:/^${value}$/`;}, 
        contains(value) { return value; }, 
        startswith(value) { return `name:/^${value}/`;}, 
        endswith(value) { return `name:/${value}$/`;},
      },
    },
    
    contains: {  
      mapped: "oracletext",
      translations: { contains: "o:" },
    }, 
    oracletext: {  
      shorthand: ["o"],
      keywords: ['is', 'contains', 'startsWith', 'endsWith'],
      translations: { 
        is(value) { return `o:/^${value}$/`;}, 
        contains(value) { return `o:${value}`; }, 
        startswith(value) { return `o:/^${value}/`;}, 
        endswith(value) { return `o:/${value}$/`;},
       },
    },

    from: {
      mappedField: "set",
      translations: { from: "set:" }
    },    
    set: {  
      keywords: ['is'],
      translations: { is: "set:", from: "set:" },
    },

    has: {
      mappedField: "keyword",
      translations: { has: "keyword=" }
    },
    keyword: {  
      ignore: true,
      translations: { has: "keyword=" },
    },
  },
  operators: {
    and: "AND",
    or: "OR",
    not: "NOT"
  }
};
  