export default class QueryConverter {
    constructor(grammar, data) {
      this.grammar = grammar;
      this.data = data;
    }

    convert(node) {
        if (node.type === 'term') {
            return this.convertTerm(node);
        }

        if (node.type === 'unary') {
            const operand = this.convert(node.operand);
            return `-(${operand})`;
        }

        if (node.type === 'binary') {
            const left = this.convert(node.left);
            const right = this.convert(node.right);
            const operator = node.operator.toUpperCase();
            return `(${left} ${operator} ${right})`;
        }

        throw new Error('Unknown node type');
    }
    
    convertTerm(node) {
        const field = this.grammar.fields[node.field];
        if(field) {
            const translations = field.translations[node.keyword];
            if(translations) {
                if(typeof translations === 'function') {
                    return translations(node.value);
                }
                return `${translations}${node.value}`;
            }
        }
        return `${node.field}${node.value}`;
    }
}